
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin: 0px 10px 0px 10px;
}

.topnav {
  overflow: hidden;
  background-color: white;
  margin-top: 20px;
}
.topnav a.logo {
  float: left;
}
.topnav .links {
  margin-top: 35px;
  float: right;
}
.topnav .links a {
  display: inline-block;
  color: grey;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  font-weight: bold;
}
.topnav .links a:hover {
  background-color: #ddd;
}
.topnav .links .icon {
  display: none;
}
@media screen and (max-width: 600px) {
.topnav .links a {
    display: none;
}
.topnav .links a.icon {
    float: right;
    display: block;
}
.topnav.responsive {
    position: relative;
}
.topnav.responsive .links .icon {
    position: absolute;
    right: 0;
    top: 0;
}
.topnav.responsive .links a {
    float: none;
    display: block;
    text-align: left;
}
}

